import "./App.css";
import React, { useEffect } from "react";
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import Appbar from "./Appbar";
import { Input } from "@mui/material";
import { ItemEntity, getItems, addItem, deleteItem, updateItem } from "./bloc/items";
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import DoneIcon from '@mui/icons-material/Done';
// import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function App() {
  const Items = ItemEntity.use();
  const [edit, setEdit] = React.useState(-1);
  const [editItem, setEditItem] = React.useState({});
  useEffect(() => { getItems() }, []);

  return (
    <>
      <Appbar />
      <div style={{ padding: "28px" }} />
      {Items &&
        Items.map((item) => (
          <div key={item.item_id}>
            <Card style={{ padding: "28px", margin: "20px" }}>
              {edit === item.item_id ?
                (
                  <>
                  <div style={{ display: "flex", justifyContent: "space-between" }}>
                    <h2>
                      <Input type="text" name="name" placeholder="Ekipman Ad" defaultValue={item.name} onChange={(e) => {
                        setEditItem({ ...editItem, name: e.target.value });
                      }} />
                    </h2>
                    <p>
                      <Input type="text" name="location" placeholder="Yer" defaultValue={item.location} onChange={(e) => {
                        setEditItem({ ...editItem, location: e.target.value });
                      }} />
                    </p>
                  </div>
                      <Input type="text" name="description" placeholder="Aklama" defaultValue={item.description} onChange={(e) => {
                        setEditItem({ ...editItem, description: e.target.value });
                      }} />
                    <div style={{ display: "flex", justifyContent: "flex-end" }}>
                      <Button variant="contained" onClick={() => deleteItem(item.item_id)}
                        style={{ backgroundColor: "red", color: "white" }}
                      >
                        <DeleteIcon>delete</DeleteIcon>
                      </Button>
                      <span style={{ padding: "5px" }} />
                      <Button variant="contained" onClick={() => {
                        setEdit(-1);
                        updateItem(editItem);
                      }}>
                        <DoneIcon>done</DoneIcon>
                      </Button>
                    </div>
                  </>
                ) : (
                  <>
                  <div style={{ display: "flex", justifyContent: "space-between" }}>
                    <h2>{item.name}</h2>
                    <p>{item.location}</p>
                  </div>
                    <p>{item.description}</p>
                    <div style={{ display: "flex", justifyContent: "flex-end" }}>
                      <Button variant="contained" onClick={() => deleteItem(item.item_id)}
                        style={{ backgroundColor: "red", color: "white" }}
                      >
                        <DeleteIcon>delete</DeleteIcon>
                      </Button>
                      <span style={{ padding: "5px" }} />
                      <Button variant="contained" onClick={() => {
                        setEdit(item.item_id);
                        setEditItem(item);
                      }}>
                        <EditIcon>edit</EditIcon>
                      </Button>
                    </div>
                  </>
                )
              }
            </Card>
          </div>
        ))}
      <Card style={{ padding: "30px", margin: "15px", position: "fixed", bottom: "0", right: "0", border: "1px solid black" }}>
        <h2>Yeni Ekipman Ekle</h2>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            addItem(e.target.name.value, e.target.description.value);
            e.target.name.value = "";
            e.target.description.value = "";
          }}
        >
          <Input type="text" name="name" placeholder="Ekipman Ad" />
          <span style={{ padding: "10px" }} />
          <Input type="text" name="description" placeholder="Aklama" />
          <div style={{ padding: "10px" }} />
          <Input type="text" name="location" placeholder="Yer" />
          <span style={{ padding: "10px" }} />
          <Button variant="contained" type="submit">Ekle</Button>
        </form>
      </Card>
    </>
  )
}
